/* global Swiper jQuery bodymovin Vimeo */

/*
 * Swiper for customer quotes in hero
 */
(function () {
	"use strict";

	new Swiper(".heroBox .swiper-container", {
		// Optional parameters
		loop: true,

		// If we need pagination
		pagination: {
			el: ".heroBox .swiper-pagination",
			clickable: true
		},
		autoplay: {
			delay: 5000,
			disableOnInteraction: false,
			pauseOnMouseEnter: true
		},
	});

}());

// Resources slider

(function () {

	"use strict";

	// init news and events carousel
	new Swiper(".resources .resources-slider-container", {
		slidesPerView: "auto",
		navigation: false,
		pagination: {
			el: ".resources .swiper-pagination",
			type: "bullets",
		},
		centeredSlides: false,
		spaceBetween: 30
	});

}());

/*
 * Play video if specific url hash exists
 */
(function() {
	"use strict";

	/*
	 * handle hash change
	 */
	function handleHashChange () {

		var hash;

		// get hash value
		if (window.location.hash) {

			hash = window.location.hash.substring(1);
			if (hash === "vmdr-video") {
				var poster = document.querySelector(".poster");
				var playIcon = document.querySelector(".play-icon");
				var playOverlay = document.querySelector(".q-video-poster-overlay");
				var iframe = document.querySelector("iframe");
				var player = new Vimeo.Player(iframe);
				poster.classList.add("hidden");
				playIcon.classList.add("hidden");
				playOverlay.classList.add("hidden");
				player.play();
			}

		}
	}

	window.addEventListener("hashchange", handleHashChange);
	document.addEventListener("DOMContentLoaded", handleHashChange);
} ());

(function ($) {

	"use strict";


	$(".q-apps-screenshot__list_and_icons h4").each( function(){
		var $header =  $(this);
		var $section =  $header.next(".section");
		$header.on("click", function() {
			$header.toggleClass("expanded2");
			$section.slideToggle();
		});
	});

	// video overlay hide on play

	$(".play-icon").on("click", function(){
		var poster = document.querySelector(".poster");
		var playIcon = document.querySelector(".play-icon");
		var playOverlay = document.querySelector(".q-video-poster-overlay");
		var iframe = document.querySelector("iframe");
		var player = new Vimeo.Player(iframe);
		poster.classList.add("hidden");
		playIcon.classList.add("hidden");
		playOverlay.classList.add("hidden");
		player.play();
		$(".video-overlay").addClass("hidden");
	});


}(jQuery));


